import React, { useCallback, useEffect } from 'react';
import { useField } from 'formik';
import { useTheme } from '@material-ui/core';
import MonacoEditor from 'react-monaco-editor';
import { languages } from 'monaco-editor/esm/vs/editor/editor.api';
import './monaco';
/* eslint-disable import/no-webpack-loader-syntax */
// NOTE: using loader syntax becuase Yaml worker imports editor.worker directly and that
// import shouldn't go through loader syntax.
import EditorWorker from 'worker-loader!monaco-editor/esm/vs/editor/editor.worker';
import YamlWorker from 'worker-loader!monaco-yaml/esm/yaml.worker';
window.MonacoEnvironment = {
    getWorker(_, label) {
        if (label === 'yaml') {
            return new YamlWorker();
        }
        return new EditorWorker();
    },
};
const { yaml } = (languages ?? {});
const Editor = ({ name, schemas }) => {
    const options = {
        selectOnLineNumbers: true,
        minimap: {
            enabled: false,
        },
        scrollBeyondLastLine: false,
        tabSize: 2,
    };
    const theme = useTheme();
    const [{ value }, , { setValue }] = useField(name);
    useEffect(() => {
        yaml?.yamlDefaults.setDiagnosticsOptions({
            validate: true,
            schemas,
            enableSchemaRequest: true,
            hover: true,
            completion: true,
        });
    }, [schemas]);
    const handleChange = useCallback((v) => setValue(v), [setValue]);
    return (<MonacoEditor language="yaml" theme={theme.palette.type} options={options} value={value} onChange={handleChange}/>);
};
export default Editor;
