import { useEffect, useState, useCallback } from 'react';
import YAML from 'yaml';
import { useFlexgetAPI } from 'core/api';
import { Method } from 'utils/fetch';
import { useGlobalInfo } from 'core/status/hooks';
import { atobUTF16, btoaUTF16 } from 'utils/encoding';
import { TaskContainer } from 'plugins/tasks/hooks';
import { useContainer } from 'unstated-next';
export const useGetConfig = () => {
    const [get, getRequest] = useFlexgetAPI('/server/raw_config');
    const [post, postRequest] = useFlexgetAPI('/server/raw_config', Method.Post);
    const [config, setConfig] = useState('');
    const { refresh } = useContainer(TaskContainer);
    const pushInfo = useGlobalInfo();
    useEffect(() => {
        const fn = async () => {
            const resp = await getRequest();
            if (resp.ok) {
                setConfig(atobUTF16(resp.data.rawConfig));
            }
        };
        fn();
    }, [getRequest]);
    const saveConfig = useCallback(async (v) => {
        const resp = await postRequest({ rawConfig: btoaUTF16(v) });
        if (resp.ok) {
            setConfig(v);
            pushInfo(resp.data.message);
            refresh();
        }
        return resp;
    }, [postRequest, pushInfo, refresh]);
    return [{ config, state: { get, post } }, saveConfig];
};
export const useGetVariables = () => {
    const [get, getRequest] = useFlexgetAPI('/variables');
    const [post, postRequest] = useFlexgetAPI('/variables', Method.Put);
    const [variables, setVariables] = useState('');
    const pushInfo = useGlobalInfo();
    useEffect(() => {
        const fn = async () => {
            const resp = await getRequest();
            if (resp.ok) {
                setVariables(YAML.stringify(resp.data));
            }
        };
        fn();
    }, [getRequest]);
    const saveVariables = useCallback(async (v) => {
        const resp = await postRequest(YAML.parse(v));
        if (resp.ok) {
            setVariables(v);
            pushInfo('Variables successfully saved');
        }
        return resp;
    }, [postRequest, pushInfo]);
    return [{ variables, state: { get, post } }, saveVariables];
};
export const useGetSchema = () => {
    const [state, getRequest] = useFlexgetAPI('/schema', Method.Get, true);
    const [schemas, setSchemas] = useState([]);
    useEffect(() => {
        const fn = async () => {
            const resp = await getRequest();
            if (resp.ok) {
                setSchemas(resp.data.schemas.map(schema => ({
                    uri: schema.id,
                    fileMatch: schema.id === `${document.baseURI}api/schema/config` ? ['*'] : undefined,
                    schema,
                })));
            }
        };
        fn();
    }, [getRequest]);
    return { ...state, schemas };
};
