import React, { useEffect } from 'react';
import { useFormikContext } from 'formik';
import { Button } from '@material-ui/core';
const SubmitButton = ({ loading, children, ...props }) => {
    const { submitForm, isSubmitting, setSubmitting } = useFormikContext();
    useEffect(() => {
        setSubmitting(loading);
    }, [loading, setSubmitting]);
    return (<Button {...props} variant="contained" color="primary" onClick={submitForm} disabled={isSubmitting} type="submit">
      {children}
    </Button>);
};
export default SubmitButton;
